// eslint-disable-next-line import/no-unresolved
import { createNamespacedHelpers } from 'vuex';

const { mapActions } = createNamespacedHelpers('storeCommodity');
export default {
  data() {
    return {
      private_price_control_info: {},
      styleArr: {
        color: 'red',
      },
    };
  },
  onLoad() {
    this.PRICE_CONTROL_INFO({
      url: this.$api.apiUrl.POST_BRAND_LIST,
      data: {
        page: 1,
        num: 10,
      },
    }).then((res) => {
		if(res.code === 1){
      		this.private_price_control_info = res.msg;
		}else{
			this.$api.msg(res.msg)
		}
	});
	// 可分享当前页面
	uni.showShareMenu({
		withShareTicket:true,
    });
  },
  methods: {
	...mapActions(['PRICE_CONTROL_INFO']),
	navToJoin(){
		// 还在开发中
		// uni.navigateTo({
		// 	url:"/pages/mys/controlledpricesdetails/controlledpricesdetails"
		// })
	}
  },
  computed: {
  },
};
